:root{
  --red: #e30613;
  --red2: #ff2a2a;

  --text: #171717;
  --muted: rgba(23,23,23,0.70);

  --bg: #f7f7f7;
  --card: #ffffff;

  --border: rgba(17,17,17,0.12);
  --shadow: 0 18px 42px rgba(17,17,17,0.10);
  --shadow2: 0 22px 60px rgba(17,17,17,0.12);

  --container: 1200px;
  --radius: 18px;
}

*{ box-sizing: border-box; }
html, body{ height: 100%; }

body{
  margin:0;
  font-family: Poppins, system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  color: var(--text);
  background:
    radial-gradient(circle at 12% 10%, rgba(227,6,19,0.08), transparent 40%),
    radial-gradient(circle at 90% 20%, rgba(0,0,0,0.06), transparent 44%),
    linear-gradient(180deg, #fff, var(--bg));
}

.page{ min-height: 100vh; }

/* =========================
   TOPBAR
   ========================= */
.topbar{
  position: sticky;
  top: 0;
  z-index: 20;
  background: rgba(255,255,255,0.88);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(17,17,17,0.08);
  padding: 10px 14px;
}

.topbar .container{
  max-width: var(--container);
  margin: 0 auto;
  display:flex;
  justify-content: space-between;
  align-items:center;
  gap: 12px;
}

/* dacă nu ai wrapper .container în JSX, păstrăm și fallback */
.topbar{
  display:flex;
  justify-content: space-between;
  align-items:center;
  gap: 12px;
}

.brand{
  display:flex;
  align-items:center;
  gap: 10px;
  min-width: 220px;
}
.brandLogo{
  width: 42px;
  height: 42px;
  border-radius: 14px;
  object-fit: cover;
  border: 1px solid rgba(17,17,17,0.10);
  box-shadow: 0 12px 24px rgba(17,17,17,0.12);
}
.brandTitle{ font-weight: 950; line-height: 1.1; }
.brandSub{ font-size: 12px; color: var(--muted); margin-top: 2px; }

.topbarRight{
  display:flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.tag{
  font-size: 12px;
  font-weight: 950;
  padding: 7px 10px;
  border-radius: 999px;
  background: rgba(227,6,19,0.08);
  border: 1px solid rgba(227,6,19,0.16);
}

/* =========================
   LAYOUT CONTAINER
   ========================= */
.wrap{
  max-width: var(--container);
  margin: 0 auto;
  padding: 14px 14px 24px;
}

.grid{
  display:grid;
  grid-template-columns: 420px 1fr;
  gap: 12px;
  align-items: start;
}

/* Cards */
.card{
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}

/* =========================
   LEFT CARD
   ========================= */
.leftCard{ padding: 14px; }

.heroRow{
  display:flex;
  gap: 12px;
  justify-content: space-between;
  align-items:flex-start;
}

.h1{
  margin: 2px 0 6px;
  font-size: 20px;
  font-weight: 950;
}
.lead{
  margin: 0;
  color: var(--muted);
  line-height: 1.5;
  font-size: 13px;
}

.progressMini{
  min-width: 120px;
  text-align: right;
}
.pmTop{
  display:flex;
  justify-content: space-between;
  gap: 8px;
  font-size: 12px;
  color: var(--muted);
}
.pmBar{
  height: 10px;
  border-radius: 999px;
  background: rgba(17,17,17,0.08);
  overflow: hidden;
  margin-top: 6px;
}
.pmFill{
  height: 100%;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--red), var(--red2));
}

/* carusel container */
.mediaCard{
  margin-top: 10px;
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid rgba(17,17,17,0.10);
  box-shadow: var(--shadow2);
  background: #fff;

  /* ✅ pe desktop nu-l lăsăm să “respire” ciudat */
  width: 100%;
  max-width: 100%;
}

/* mini cards */
.mini3{
  margin-top: 10px;
  display:grid;
  gap: 10px;
}
.miniCard{
  display:flex;
  gap: 10px;
  align-items:center;
  padding: 10px;
  border-radius: 16px;
  border: 1px solid rgba(17,17,17,0.10);
  background: linear-gradient(135deg, rgba(227,6,19,0.06), rgba(0,0,0,0.01));
}
.miniIcon{
  width: 42px;
  height: 42px;
  border-radius: 14px;
  display:grid;
  place-items:center;
  background: rgba(227,6,19,0.10);
  border: 1px solid rgba(227,6,19,0.18);
}
.miniTitle{ font-weight: 950; font-size: 13px; }
.miniSub{ font-size: 12px; color: var(--muted); margin-top: 2px; }

.note{
  margin-top: 10px;
  font-size: 12px;
  color: var(--muted);
  border-left: 3px solid rgba(227,6,19,0.22);
  padding-left: 10px;
}

/* =========================
   RIGHT / FORM CARD
   ========================= */
.formCard{ padding: 14px; }

.formHead{
  display:flex;
  justify-content: space-between;
  align-items:flex-start;
  gap: 12px;
  margin-bottom: 10px;
}

.kicker{
  display:inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 950;
  border: 1px solid rgba(17,17,17,0.10);
  background: rgba(0,0,0,0.04);
}

.h2Row{
  display:flex;
  align-items:center;
  gap: 10px;
  margin-top: 8px;
}
.h2{ font-size: 18px; font-weight: 950; }
.badge{
  font-size: 12px;
  font-weight: 950;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(227,6,19,0.08);
  border: 1px solid rgba(227,6,19,0.16);
}

.muted{ color: var(--muted); font-size: 13px; margin-top: 4px; }
.small{ font-size: 12px; }

/* Step tabs */
.stepTabs{ display:flex; gap: 8px; }
.tab{
  width: 40px;
  height: 40px;
  border-radius: 14px;
  border: 1px solid rgba(17,17,17,0.12);
  background: #fff;
  font-weight: 950;
  cursor: pointer;
}
.tab.active{
  border-color: rgba(227,6,19,0.22);
  background: linear-gradient(135deg, rgba(227,6,19,0.08), rgba(0,0,0,0.01));
  box-shadow: 0 14px 26px rgba(227,6,19,0.10);
}
.tab:disabled{ opacity: 0.45; cursor: not-allowed; }

/* Sections + layout */
.sectionTitle{
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid rgba(17,17,17,0.08);
  font-weight: 950;
}

.row2{
  display:grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  align-items:end;
  margin-top: 10px;
}

/* Fields */
.field{ display:flex; flex-direction: column; gap: 6px; }
.labelRow{
  display:flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 10px;
}
.label{ font-weight: 950; font-size: 13px; }
.req{ color: var(--red); }
.hint{ font-size: 12px; color: var(--muted); }

input, select{
  height: 44px;
  border-radius: 14px;
  border: 1px solid rgba(17,17,17,0.14);
  padding: 0 12px;
  font-size: 14px;
  outline: none;
  background: #fff;
  transition: box-shadow .15s, transform .12s, border-color .15s;
}
input:focus, select:focus{
  border-color: rgba(227,6,19,0.30);
  box-shadow: 0 0 0 5px rgba(227,6,19,0.08);
  transform: translateY(-1px);
}

.microTip{
  padding: 10px 12px;
  border-radius: 14px;
  border: 1px dashed rgba(17,17,17,0.16);
  background: rgba(0,0,0,0.02);
  color: var(--muted);
  font-size: 12px;
  line-height: 1.4;
}

/* ✅ Signature – stable, no offset */
.sigCard{
  margin-top: 12px;
  padding: 12px;
  border-radius: 16px;
  border: 1px solid rgba(17,17,17,0.10);
  background: #fff;
}
.sigHead{
  display:flex;
  justify-content: space-between;
  align-items:flex-start;
  gap: 10px;
  margin-bottom: 10px;
}
.sigTitle{ font-weight: 950; }

.sigFrame{
  width: 100%;
  border-radius: 16px;
  border: 2px dashed rgba(227,6,19,0.34);
  background: #fff;
  box-shadow: 0 16px 30px rgba(17,17,17,0.08);
  overflow: hidden;

  touch-action: none;
  -ms-touch-action: none;
}

.sigCanvas{
  display:block;
  touch-action: none;
  -ms-touch-action: none;
}

/* Checks */
.checks{
  margin-top: 10px;
  display:grid;
  gap: 10px;
}
.check{
  display:flex;
  gap: 10px;
  align-items:flex-start;
  padding: 10px 12px;
  border-radius: 16px;
  border: 1px solid rgba(17,17,17,0.10);
  background: #fff;
}
.check input{ margin-top: 3px; accent-color: var(--red); }
.check span{ font-size: 13px; color: var(--muted); line-height: 1.4; }

/* Buttons */
.actions{
  margin-top: 12px;
  display:flex;
  justify-content: flex-end;
}
.actions.split{
  justify-content: space-between;
  gap: 10px;
}
.btn{
  height: 46px;
  border-radius: 14px;
  padding: 0 14px;
  font-weight: 950;
  border: 1px solid rgba(17,17,17,0.12);
  background: #fff;
  cursor: pointer;
}
.btn.primary{
  border: none;
  color: #fff;
  background: linear-gradient(135deg, var(--red), var(--red2));
  box-shadow: 0 16px 28px rgba(227,6,19,0.16);
}
.btn.primary:disabled{
  opacity: 0.6;
  cursor: not-allowed;
  box-shadow: none;
}
.btn.light:hover, .btn.ghost:hover{
  box-shadow: 0 14px 26px rgba(17,17,17,0.08);
}

/* =========================
   FOOTER (single source of truth)
   ========================= */
.footer{
  margin-top: 48px;
  padding: 32px 14px 18px;
  background: linear-gradient(180deg, #ffffff 0%, #fafafa 30%, #f4f4f4 100%);
  border-top: 1px solid rgba(0,0,0,0.08);
}

.footerInner{
  max-width: var(--container);
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1.2fr 1fr 1fr;
  gap: 32px;
  align-items: flex-start;
}

.footerCol{
  display:flex;
  flex-direction: column;
  gap: 10px;
}

.footerTitle{
  font-size: 14px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  color: #111;
  position: relative;
}
.footerTitle::after{
  content:"";
  display:block;
  width: 28px;
  height: 3px;
  margin-top: 6px;
  border-radius: 3px;
  background: linear-gradient(135deg, var(--red), var(--red2));
}

.footerText{
  font-size: 14px;
  line-height: 1.6;
  color: #555;
  max-width: 420px;
}

.footerList{
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 14px;
  color: #333;
}
.footerList li{
  display:flex;
  align-items:center;
  gap: 8px;
  margin-bottom: 6px;
}

.footer a{
  color: var(--red);
  text-decoration: none;
  font-weight: 600;
}
.footer a:hover{ text-decoration: underline; }

.socials{
  display:flex;
  gap: 12px;
  margin-top: 6px;
}
.socials a{
  padding: 6px 14px;
  font-size: 13px;
  border-radius: 999px;
  border: 1px solid rgba(227,6,19,0.35);
  background: rgba(227,6,19,0.06);
  color: var(--red);
  transition: all 0.2s ease;
}
.socials a:hover{
  background: rgba(227,6,19,0.12);
  transform: translateY(-1px);
}

.footerBottom{
  max-width: var(--container);
  margin: 0 auto;
  margin-top: 26px;
  padding-top: 14px;
  border-top: 1px dashed rgba(0,0,0,0.12);
  text-align: center;
  font-size: 12.5px;
  color: #666;
}
.sep{
  margin: 0 6px;
  color: #aaa;
}

/* =========================
   CAROUSEL (if you keep here)
   ========================= */
.c2 { margin-top: 0; }

.c2Frame{
  position: relative;
  border-radius: 18px;
  overflow: hidden;
  border: 1px solid rgba(31,35,48,0.10);
  box-shadow: 0 14px 28px rgba(31,35,48,0.10);
  background: #fff;
  width: 100%;
  aspect-ratio: 16 / 10;
}

.c2Slide{
  position: relative;
  width: 100%;
  height: 100%;
  display:grid;
  place-items:center;
  background:#fff;
}

.c2Img{
  width:100%;
  height:100%;
  display:block;
  object-fit: cover;
  object-position: center;
  transform: none;
}

.c2Caption{
  position:absolute;
  left:10px;
  right:10px;
  bottom:10px;
  padding:10px 12px;
  border-radius:16px;
  background: rgba(255,255,255,0.86);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(31,35,48,0.10);
}

.c2Title{ font-weight: 900; font-size: 13px; }
.c2Text{ font-size: 12px; color: rgba(31,35,48,0.75); margin-top: 2px; }

.c2Arrow{
  position:absolute;
  top:50%;
  transform: translateY(-50%);
  z-index: 5;
  width:42px;
  height:42px;
  border-radius:14px;
  border:1px solid rgba(31,35,48,0.10);
  background: rgba(255,255,255,0.88);
  backdrop-filter: blur(10px);
  cursor:pointer;
  font-size:28px;
  line-height:42px;
  display:grid;
  place-items:center;
}
.c2Arrow.left{ left:10px; }
.c2Arrow.right{ right:10px; }
.c2Arrow:hover{ box-shadow: 0 12px 22px rgba(31,35,48,0.10); }

.c2Dots{
  display:flex;
  justify-content:center;
  gap:8px;
  margin-top: 10px;
}
.c2Dot{
  width:10px;
  height:10px;
  border-radius:999px;
  border:none;
  cursor:pointer;
  background: rgba(31,35,48,0.18);
}
.c2Dot.active{
  background: linear-gradient(135deg, #ff4d6d, #4f46e5);
  width:22px;
}

/* =========================
   RESPONSIVE
   ========================= */
@media (max-width: 980px){
  .grid{ grid-template-columns: 1fr; }

  .progressMini{
    text-align: left;
    min-width: unset;
    width: 100%;
  }
  .heroRow{ flex-direction: column; }

  /* ✅ IMPORTANT: pe mobil caruselul NU are voie să crească haotic */
  .mediaCard{
    /* ține-l stabil indiferent de pasul formularului */
    height: clamp(200px, 34vh, 320px);
  }
  .mediaCard .c2Frame{
    height: 100%;
    aspect-ratio: auto;
  }

  /* footer stack */
  .footerInner{
    grid-template-columns: 1fr;
    gap: 24px;
  }
  .footerText{ max-width: 100%; }
}

@media (max-width: 640px){
  .row2{ grid-template-columns: 1fr; }

  .actions{
    position: sticky;
    bottom: 10px;
    background: rgba(255,255,255,0.92);
    backdrop-filter: blur(10px);
    padding: 10px;
    border-radius: 18px;
    border: 1px solid rgba(17,17,17,0.10);
    box-shadow: 0 18px 36px rgba(17,17,17,0.10);
  }

  /* să nu se lipească footer de margini pe mobil */
  .footer{ padding-left: 14px; padding-right: 14px; }
}
/* ============ FIX: Mobile width overflow (Step 2) ============ */
html, body {
  width: 100%;
  overflow-x: hidden;
}

.page, .wrap {
  width: 100%;
  overflow-x: hidden;
}

/* IMPORTANT: permite elementelor din grid să se micșoreze */
.grid,
.left,
.right,
.leftCard,
.formCard {
  min-width: 0;
}

/* grid children (inputs, canvas etc.) nu trebuie să forțeze lățimea */
.row2, .field, .sigCard, .sigFrame {
  min-width: 0;
}

/* orice imagine/canvas să nu depășească */
img, canvas {
  max-width: 100%;
}
