/* wrapper */
.c2 {
  margin-top: 10px;
}

/* cadrul caruselului: aici controlăm dimensiunea */
.c2Frame {
  position: relative;
  border-radius: 18px;
  overflow: hidden;
  border: 1px solid rgba(31, 35, 48, 0.10);
  box-shadow: 0 14px 28px rgba(31, 35, 48, 0.10);
  background: #fff;

  /* ✅ chenar constant, fără “umflare” */
  aspect-ratio: 16 / 10;
  width: 100%;
}

/* dacă vrei să fie mai înalt/lat pe desktop, schimbi aici ratio-ul */
/* .c2Frame { aspect-ratio: 16 / 9; } */

/* slide-ul umple cadrul */
.c2Slide {
  position: relative;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background: #fff;
}

/* imaginea */
.c2Img {
  width: 100%;
  height: 100%;
  display: block;

  /* păstrăm “cover” ca în versiunea ta */
  object-fit: cover;
  object-position: center;

  /* fără scale care poate mări artificial */
  transform: none;
}

/* caption */
.c2Caption {
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 10px;
  padding: 10px 12px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.86);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(31, 35, 48, 0.10);
}

.c2Title {
  font-weight: 900;
  font-size: 13px;
}

.c2Text {
  font-size: 12px;
  color: rgba(31, 35, 48, 0.75);
  margin-top: 2px;
}

/* arrows */
.c2Arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5;

  width: 42px;
  height: 42px;
  border-radius: 14px;
  border: 1px solid rgba(31, 35, 48, 0.10);
  background: rgba(255, 255, 255, 0.88);
  backdrop-filter: blur(10px);
  cursor: pointer;

  font-size: 28px;
  line-height: 42px;
  display: grid;
  place-items: center;
}

.c2Arrow.left { left: 10px; }
.c2Arrow.right { right: 10px; }

.c2Arrow:hover {
  box-shadow: 0 12px 22px rgba(31, 35, 48, 0.10);
}

/* dots */
.c2Dots {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 10px;
}

.c2Dot {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  border: none;
  cursor: pointer;
  background: rgba(31, 35, 48, 0.18);
}

.c2Dot.active {
  background: linear-gradient(135deg, #ff4d6d, #4f46e5);
  width: 22px;
}

/* ✅ FIX MOBILE: pe telefon nu lăsăm caruselul să devină “uriaș” */
@media (max-width: 980px) {
  .c2Frame {
    /* ține înălțimea în frâu indiferent de cât e formularul */
    height: clamp(200px, 34vh, 320px);
    aspect-ratio: auto; /* height devine “șeful” pe mobil */
  }

  .c2Arrow {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    font-size: 26px;
  }

  .c2Caption {
    padding: 9px 10px;
    border-radius: 14px;
  }
}
