:root{
  --red: #e30613;
  --red2: #ff2a2a;

  --text: #171717;
  --muted: rgba(23,23,23,0.70);

  --bg: #f7f7f7;
  --card: #ffffff;

  --border: rgba(17,17,17,0.12);
  --shadow: 0 18px 42px rgba(17,17,17,0.10);
}

.success-container{
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: var(--bg);
}

.success-card{
  width: 100%;
  max-width: 420px;
  background-color: var(--card);
  padding: 40px 32px;
  border-radius: 14px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border);
  text-align: center;
}

.success-logo{
  width: 64px;
  height: auto;
  margin-bottom: 24px;
}

.success-title{
  font-size: 2rem;
  font-weight: 800;
  color: var(--text);
  margin-bottom: 12px;
}

.success-message{
  font-size: 1rem;
  color: var(--muted);
  margin-bottom: 28px;
}

.checkmark{
  width: 76px;
  height: 76px;
  margin: 0 auto 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--red);
}

.thank-you-note{
  font-size: 1rem;
  font-weight: 600;
  color: var(--text);
  margin-bottom: 32px;
}

.back-home-btn{
  display: inline-block;
  padding: 12px 22px;
  font-size: 1rem;
  font-weight: 700;
  background-color: var(--red);
  color: #ffffff;
  border-radius: 8px;
  text-decoration: none;
  transition: background-color 0.2s ease, transform 0.15s ease;
}

.back-home-btn:hover{
  background-color: var(--red2);
  transform: translateY(-1px);
}

/* Mobile */
@media (max-width: 480px){
  .success-card{
    padding: 32px 20px;
  }

  .success-title{
    font-size: 1.7rem;
  }
}
